import { get } from 'svelte/store'
import { getTokenAsync } from '../helpers/auth'
import { urlData } from '../stores/router'
import { snackList } from '../stores/main'
import { fetchBase } from './index'

function loadList(set) {
  const { project } = get(urlData).query
  const segment = get(urlData).segment
  if (segment !== 'templates') return (window.listenFieldListWait = false)

  if (!project)
    return setTimeout(() => {
      window.dispatchEvent(new CustomEvent('reload-template-list'))
    }, 200)

  snackList.add({ text: 'Carregando lista de modelos!' })
  getTokenAsync()
    .then(token => {
      return fetchBase(`/template/list?project=${project}`, {
        token,
      })
    })
    .then(([response, result]) => {
      if (!response.ok) {
        console.error(response.status, result)
        return (window.listenTemplateListWait = false)
      }
      if (result.status === 'success') {
        console.log('%ctemplate-list', 'color:#16f; text-transform:uppercase;')
        console.log(result.data)
        set(
          result.data.map(r => {
            return {
              name: r.name,
              slug: r.slug,
              order: r.order,
              lastUpdate: r.lastUpdate,
              created: r.created,
            }
          })
        )
      } else {
        snackList.add({ text: 'No foi possvel carregar a lista de modelos' })
      }
      window.listenTemplateListWait = false
    })
    .catch(err => {
      window.listenTemplateListWait = false
      console.error('ERROR', err)
    })
}

export function templateList(set) {
  set([])
  if (!window.listenTemplateList) {
    window.listenTemplateList = true
    window.listenTemplateListWait = false
    window.addEventListener('reload-template-list', () => {
      if (window.listenTemplateListWait) return
      window.listenTemplateListWait = true
      loadList(set)
    })
  }
  loadList(set)
}

export function templateOrdination(store) {
  const { subscribe } = store
  subscribe(state => {
    const project = get(urlData).query.project
    const segment = get(urlData).segment
    if (segment !== 'templates') return

    if (!project) return

    const toSave = state
      .map((el, i) => ({ id: el.slug, oldOrder: el.order, order: i }))
      .filter(el => el.order !== el.oldOrder)

    if (toSave.length > 0) {
      if (store.timeoutId) {
        clearTimeout(store.timeoutId)
        store.timeoutId = null
      }
      store.timeoutId = setTimeout(() => {
        store.timeoutId = null
        window.listenTemplateListWait = true
        getTokenAsync()
          .then(token => {
            const promises = []
            toSave.forEach(({ id, order }) => {
              state[order].order = order
              promises.push(
                fetchBase(`/template`, {
                  token,
                  method: 'PUT',
                  data: {
                    id,
                    order,
                    project,
                  },
                })
              )
            })
            return Promise.all(promises)
          })
          .then(result => {
            let res = result.find(el => !el[0].ok || el[1].status !== 'success')
            if (res) {
              snackList.add({
                text: 'Ocorreu um erro ao salvar a ordem dos modelos!',
              })
            }
            window.listenTemplateListWait = false
          })
          .catch(err => {
            window.listenTemplateListWait = false
            console.error('ERROR', err)
          })
      }, 2000)
    }
  })
}

export function addTemplate(template) {
  return new Promise((resolve, reject) => {
    const project = get(urlData).query.project
    if (!project) return reject()

    getTokenAsync()
      .then(token => {
        return fetchBase('/template', {
          token,
          method: 'POST',
          data: {
            ...template,
            project,
          },
        })
      })
      .then(([response, result]) => {
        if (!response.ok) {
          console.error(status, result)
          reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-template-list'))
          resolve(result.data)
        } else {
          snackList.add({ text: 'No foi possvel criar um novo modelo!' })
          reject()
        }
      })
      .catch(reject)
  })
}

export function removeTemplate(id) {
  return new Promise((resolve, reject) => {
    const project = get(urlData).query.project
    if (!project) return reject()

    getTokenAsync()
      .then(token => {
        return fetchBase('/template', {
          token,
          method: 'delete',
          data: {
            id,
            project,
          },
        })
      })
      .then(([response, result]) => {
        if (!response.ok) {
          console.error(status, result)
          return reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-template-list'))
          snackList.add({ text: 'Modelo removido com sucesso!' })
          resolve(result.data)
        } else {
          snackList.add({ text: 'No foi possvel remover o modelo!' })
          reject()
        }
      })
      .catch(reject)
  })
}
