<script>
  import Popper from 'popper.js'

  const sides = ['bottom', 'top', 'left', 'right']

  export let value
  export let position = 'bottom'
  export let container = null

  let open = false
  let anchor
  let node
  let control = null

  $: if (!!anchor && !!node) {
    if (!!control) control.destroy()
    let config = { placement: position }
    config.modifiers = {
      flip: {
        behavior: sides.filter(s => s !== position),
      },
    }

    if (!!container)
      config.modifiers.preventOverflow = {
        boundariesElement: container,
      }
    control = new Popper(anchor, node, config)
  }

  $: if (open && !!control) {
    control.update()
  }
</script>

<style>
  span {
    display: none;
    z-index: 4000;
    color: white;
    background-color: var(--color-black);
    padding: 16px;
  }
  span.open {
    display: block;
  }
</style>

<div>
  <div
    bind:this={anchor}
    on:mouseover={() => (open = true)}
    on:mouseleave={() => (open = false)}>
    <slot />
  </div>
  <span class="type-overline" class:open={open && value} bind:this={node}>
    {value}
  </span>
</div>
