import { writable } from 'svelte/store'
import { fetchBase } from '../api'

export const searchTitle = writable('')

function createSnackList() {
  let { set, update, subscribe } = writable([])
  let index = 0
  const res = {
    subscribe,
    set,
    clean: () => set([]),
    /**
     * @param {{text:String}} obj
     */
    add: obj => {
      obj.__id = index++
      update(v => [...v, obj])
      if (index > 10000) index = 0

      setTimeout(() => {
        res.remove(obj.__id)
      }, obj.timeout || 2000)
    },
    /**
     * <data>.__id
     * @param {String} id
     */
    remove: id => update(v => v.filter(e => e.__id !== id)),
  }

  return res
}

export const snackList = createSnackList()

export const fetchCount = writable(0)

function createModalList() {
  let { set, update, subscribe } = writable([])
  let index = 0
  const res = {
    subscribe,
    set,
    clean: () => set([]),
    /**
     * @param {{title:String,content:String|Object,actions:[Object]}} obj
     */
    add: obj => {
      obj.__id = index++
      update(v => [...v, obj])
      if (index > 10000) index = 0
    },
    /**
     * <data>.__id
     * @param {String} id
     */
    remove: id => update(v => v.filter(e => e.__id !== id)),
  }

  return res
}

export const modalDataList = createModalList()

