import { getTokenAsync } from '../helpers/auth'
import { snackList } from '../stores/main'
import { fetchBase } from './index'

function loadList(set) {
  snackList.add({ text: 'Carregando lista de projetos!' })
  getTokenAsync()
    .then(token => {
      return fetchBase('/project/list?sort=-lastUpdate.date', {
        token,
      })
    })
    .then(([response, result]) => {
      if (!response.ok) {
        console.error(response.status, result)
        return (window.listenProjectListWait = false)
      }
      if (result.status === 'success') {
        console.log('%cproject-list', 'color:#16f; text-transform:uppercase;')
        console.log(result.data)
        set(
          result.data.map(r => {
            window.clientSocket.emit('join_room', r.slug)
            return {
              name: r.name,
              slug: r.slug,
              primaryColor: r.primaryColor,
              secondaryColor: r.secondaryColor,
              properties: r.properties,
              lastUpdate: r.lastUpdate,
              created: r.created,
            }
          })
        )
      } else {
        snackList.add({ text: 'No foi possvel carregar a lista de projetos' })
      }
      window.listenProjectListWait = false
    })
    .catch(err => {
      window.listenProjectListWait = false
      console.error('ERROR', err)
    })
}

export function projectList(set) {
  if (!window.listenProjectList) {
    window.listenProjectList = true
    window.listenProjectListWait = false
    window.addEventListener('reload-project-list', () => {
      if (window.listenProjectListWait) return
      window.listenProjectListWait = true
      loadList(set)
    })
  }
  loadList(set)
}

export function addProject(project) {
  return new Promise((resolve, reject) => {
    getTokenAsync()
      .then(token => {
        return fetchBase('/project', {
          token,
          method: 'POST',
          data: project,
        })
      })
      .then(([response, result]) => {
        if (!response.ok) {
          console.error(response.status, result)
          return reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-project-list'))
          resolve(result.data)
        } else {
          snackList.add({ text: 'No foi possvel criar um novo projeto!' })
          reject()
        }
      })
      .catch(reject)
  })
}

export function removeProject(id) {
  return new Promise((resolve, reject) => {
    getTokenAsync()
      .then(token => {
        return fetchBase('/project', {
          token,
          method: 'DELETE',
          data: { id },
        })
      })
      .then(([response, result]) => {
        if (!response.ok) {
          console.error(response.status, result)
          return reject()
        }
        if (result.status === 'success') {
          snackList.add({ text: 'Projeto removido com sucesso!' })
          window.dispatchEvent(new CustomEvent('reload-project-list'))
          resolve(result.data)
        } else {
          snackList.add({ text: 'No foi possvel remover o projeto!' })
          reject()
        }
      })
      .catch(reject)
  })
}
