import { get } from 'svelte/store'
import { getTokenAsync } from '../helpers/auth'
import { urlData } from '../stores/router'
import { snackList } from '../stores/main'
import { fetchBase } from './index'

function loadList(set) {
  const { project, template } = get(urlData).query
  const segment = get(urlData).segment

  if (segment !== 'templates' || !template)
    return (window.listenFieldListWait = false)

  if (!project)
    return setTimeout(() => {
      window.dispatchEvent(new CustomEvent('reload-field-list'))
    }, 200)

  snackList.add({ text: 'Carregando lista de campos!' })
  getTokenAsync()
    .then(token => {
      return fetchBase(`/field/list?project=${project}&template=${template}`, {
        token,
      })
    })
    .then(([response, result]) => {
      if (!response.ok) {
        console.error(response.status, result)
        return (window.listenFieldListWait = false)
      }
      if (result.status === 'success') {
        console.log('%cfield-list', 'color:#16f; text-transform:uppercase;')
        console.log(result.data)
        set(
          result.data.map(r => {
            return {
              name: r.name,
              slug: r.slug,
              order: r.order,
              extension: r.extension,
              extensionType: r.extensionType,
              data: r.data,
              lastUpdate: r.lastUpdate,
              created: r.created,
            }
          })
        )
      } else {
        snackList.add({ text: 'No foi possvel carregar a lista de campos' })
      }
      window.listenFieldListWait = false
    })
    .catch(err => {
      window.listenFieldListWait = false
      console.error('ERROR', err)
    })
}

export function fieldList(set) {
  set([])
  if (!window.listenFieldList) {
    window.listenFieldList = true
    window.listenFieldListWait = false
    window.addEventListener('reload-field-list', () => {
      if (window.listenFieldListWait) return
      window.listenFieldListWait = true
      loadList(set)
    })
  }
  loadList(set)
}

export function fieldOrdination(store) {
  const { subscribe } = store
  subscribe(state => {
    const { project, template } = get(urlData).query
    const segment = get(urlData).segment
    if (segment !== 'templates') return

    if (!project || !template) return

    const toSave = state
      .map((el, i) => ({ id: el.slug, oldOrder: el.order, order: i }))
      .filter(el => el.order !== el.oldOrder)

    if (toSave.length > 0) {
      if (store.timeoutId) {
        clearTimeout(store.timeoutId)
        store.timeoutId = null
      }
      store.timeoutId = setTimeout(() => {
        store.timeoutId = null
        window.listenFieldListWait = true

        getTokenAsync()
          .then(token => {
            const promises = []
            toSave.forEach(({ id, order }) => {
              state[order].order = order
              promises.push(
                fetchBase(`/field`, {
                  token,
                  method: 'PUT',
                  data: {
                    id,
                    template,
                    project,
                    order,
                  },
                })
              )
            })
            return Promise.all(promises)
          })
          .then(result => {
            st
            let res = result.find(el => !el[0].ok || el[1].status !== 'success')
            if (res) {
              snackList.add({
                text: 'Ocorreu um erro ao salvar a ordem dos campos!',
              })
            }
            window.listenFieldListWait = false
          })
          .catch(err => {
            window.listenFieldListWait = false
            console.error('ERROR', err)
          })
      }, 2000)
    }
  })
}

export function addField(field) {
  return new Promise((resolve, reject) => {
    const { project, template } = get(urlData).query
    if (!project || !template) return reject()

    getTokenAsync()
      .then(token => {
        return fetchBase('/field', {
          token,
          method: 'POST',
          data: {
            template,
            project,
            ...field,
          },
        })
      })
      .then(([response, result]) => {
        if (!response.ok) {
          console.error(status, result)
          return reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-field-list'))
          resolve(result.data)
        } else {
          snackList.add({ text: 'No foi possvel criar um novo campo!' })
          reject()
        }
      })
      .catch(reject)
  })
}

export function removeField(id) {
  return new Promise((resolve, reject) => {
    const { project, template } = get(urlData).query
    if (!project || !template) return reject()

    getTokenAsync()
      .then(token => {
        return fetchBase('/field', {
          token,
          method: 'delete',
          data: {
            id,
            project,
            template,
          },
        })
      })
      .then(([response, result]) => {
        if (!response.ok) {
          console.error(status, result)
          return reject()
        }
        if (result.status === 'success') {
          window.dispatchEvent(new CustomEvent('reload-field-list'))
          snackList.add({ text: 'Campo removido com sucesso!' })
          resolve(result.data)
        } else {
          snackList.add({ text: 'No foi possvel remover o campo!' })
          reject()
        }
      })
      .catch(reject)
  })
}
